
.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    /* background-color: #000; */
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}
.header-bg{
    background: rgba(0, 0, 0, 0.8);
    transition: 0.5s;
}

.nav-menu{
    display: flex;
}
.nav-menu li{
    padding: 0 10px;
}
.nav-menu li a{
    font-size: 1.2rem;
    font-weight: 500;
}
.hamburger{
    display: none;
}
@media screen and (max-width: 1040px) {
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0,0,0,0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.3s;
    }
    .nav-menu.active{
        left: 0;
    }
    .nav-menu li{
        padding: 10px 0;
    }
    .nav-menu li a{
        font-size: 1.5rem;
    }
    .hamburger{
        display: initial;
    }
}